* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montseratt', sans-serif;
  background-color: #747373 !important;
}


nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh !important;
  background-color: rgba(73,79,82, 1);
  color:#FFF;
}

.Navlinks{
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  
}

.Navlinks li:hover{
color:#313131;

} 


form {
  margin-top: 3rem;
}

form input[name="city"],
form input[name="country"] {
  background-color: transparent;
  color: white;
  border: none;
  border-bottom: 2px solid whitesmoke;
  border-radius: 0;
}

.container {
  margin-top: 1.2rem;
 
}
.container-temp {
  margin-left:40% !important;
}

.container input,
.container ::-webkit-input-placeholder {
  color: white !important;
}
.container input:focus {
  background-color: transparent;
  box-shadow: none !important;
  border: none;
  border-bottom: 2px solid tomato;
}

.btn{
  background-color: bisque !important;
}



